
import Block from 'base/block/block'
import Scroll from 'base/scroll/scroll'
import Loading from 'base/loading/loading'
import ListCell from 'base/list-cell/list-cell'
import VHeader from 'components/v-header/v-header'
import { ERR_OK } from 'api/config'
import { getCollectArticles, deleteCollectArticles } from 'api'
import { mapActions } from 'vuex'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'


export default {
name: 'favorites',
mixins: [],
data () {
return {
list: [],
isLoading: false
}
},
computed: {},
components: {
Block,
Scroll,
VHeader,
Loading,
ListCell,
Swipeout,
SwipeoutItem,
SwipeoutButton
},
watch: {},
methods: {
_getCollectArticles () {
this.isLoading = true
getCollectArticles().then(data => {
if (data.code === ERR_OK) {
this.list = data.list
this.isLoading = false
return data
}
})
},
_deleteCollectArticles (item) {
const params = {
id: item.id
}
deleteCollectArticles(params).then(data => {
if (data.code === ERR_OK) {
this.$vux.toast.show({
text: '删除成功',
time: 500,
type: 'success'
})
const index = this.list.findIndex(value => {
return item.id === value.id
})
this.list.splice(index, 1)
}
})
},
selectItem (item) {
this.fetchDetail(item)
},
deleteItem (item) {
this._deleteCollectArticles(item)
},
...mapActions([
'fetchDetail'
])
},
created () {
this._getCollectArticles()
},
mounted () {}
}



